import { Component, Inject, OnInit, Optional, ViewChild } from '@angular/core';
import { ApiService, DataStorageService, SweetAlertService } from '@services';
import { COMMON_FUNCTION, ENDPOINTS, STATUSES, USER_ROLES, YES_NO_OPTION } from '@shared';
import { Brand } from '@models';
import { MatTableDataSource } from '@angular/material/table';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { FormListComponent } from '@fragments';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-brands',
  templateUrl: './brands.component.html',
  styleUrls: ['./brands.component.scss']
})
export class BrandsComponent implements OnInit {

  @ViewChild(FormListComponent) formListComponent: FormListComponent
  dataSource: MatTableDataSource<Brand>;
  displayedColumns: string[] = ['brandDesc', 'penalty', 'status'];
  statusList = [...STATUSES];
  yesNoList = [...YES_NO_OPTION];

  brand = new Brand();
  brandList = [];
  // productList = [];
  // categoryList = [];
  statusNoDisplay = ['FOR_APPROVAL', 'DISAPPROVED', 'APPROVED'];

  isFromModal = false;
  showApproveRejectButton = false;
  isShowApproveDisapproveButtonByAdmin = false;

  userRole = '';

  brandForm: FormGroup;

  constructor(
    private apiService: ApiService,
    private formBuilder: FormBuilder,
    private sweetAlertService: SweetAlertService,
    private dataStorage: DataStorageService,
    @Optional() public dialogRef: MatDialogRef<BrandsComponent>,
    @Optional() @Inject(MAT_DIALOG_DATA) public data: any
  ) {
    this.userRole = this.dataStorage.getUserRole();
    if (data) {
      if (data.dataForModal) {
        const dataFromModal = data.dataForModal;

        this.isFromModal = dataFromModal.isFromModal;
        this.showApproveRejectButton = dataFromModal.isUpdate;

        if (dataFromModal.isUpdate) {
          this.brand = dataFromModal.brand
        }
      }
    }
  }

  ngOnInit() {
    if (this.userRole !== USER_ROLES.ROLE_APPLICANT) {
      this.getList();
    }
    this.setFormValidator();
    // this.getProductList();
    // this.getCategoryList();
  }

  getList(): void {
    this.apiService.findAll(ENDPOINTS.brand).subscribe((res: any) => {
      console.log(res.responseData.data);
      this.brandList = res.responseData.data;
      this.dataSource = new MatTableDataSource<Brand>(res.responseData.data);
      this.dataSource.paginator = this.formListComponent.paginator;
      this.dataSource.sort = this.formListComponent.sort;
    });
  }

  save(endpoint) {
    if (this.isFromModal) {
      this.brand.status = 'FOR_APPROVAL';
    }

    if (!endpoint) {
      endpoint = ENDPOINTS.brand;
    }

    this.brand.penalty = this.setBrand(this.brand.penalty);

    this.apiService.save(endpoint, this.brand).subscribe(
      (res: any) => {
        if (res) {
          this.sweetAlertService.success(res);
          this.dataStorage.setS1NewBrand(res.responseData.data);
        }

        if (this.isFromModal) {
          this.dialogRef.close(this.dataStorage.getS1NewBrand());
        } else {
          this.resetFormValidator();
          this.getList();
        }
      }, (err: any) => {
        this.sweetAlertService.error(err);
      }, () => {
        // actions after subscription
      });
  }


  getBrand(): void {
    this.apiService.findById(ENDPOINTS.brand, this.formListComponent.idForUpdate).subscribe((res: any) => {
      if (res) {
        this.brand = res.responseData.data;
        // const removeStatusDisplay = this.statusNoDisplay.includes(this.brand.status);
        // this.isFromModal = removeStatusDisplay;
        this.showApproveRejectButton = this.brand.status === 'FOR_APPROVAL';

        this.isShowApproveDisapproveButtonByAdmin = this.brand.status === 'FOR_APPROVAL' ?
          true : false;
      }
    });
  }

  // getProductList() {
  //   this.apiService.findAll(ENDPOINTS.product).subscribe((res: any) => {
  //     this.productList = res.responseData.data.filter((product) => product.status === COMMON_FUNCTION.ACTIVE);
  //   });
  // }

  // getCategoryList(): void {
  //   this.apiService.findAll(ENDPOINTS.category).subscribe((res: any) => {
  //     this.categoryList = res.responseData.data;
  //   });
  // }

  update(endpoint) {
    if (!endpoint) {
      endpoint = ENDPOINTS.brand;
    }

    this.brand.penalty = this.setBrand(this.brand.penalty);
    this.apiService.update(endpoint, this.brand).subscribe(
      (res: any) => {
        if (res) {
          this.sweetAlertService.success(res);
          this.dataStorage.setS1NewBrand(res.responseData.data)
        }

        if (this.isFromModal) {
          this.dialogRef.close(this.dataStorage.getS1NewBrand());
        } else {
          this.resetFormValidator();
          this.getList();
        }
      }, (err: any) => {
        this.sweetAlertService.error(err);
      }, () => {
        // actions after subscription
      });
  }

  setFormValidator(): void {
    if (!this.isFromModal) {
      this.brand.status = COMMON_FUNCTION.ACTIVE;
    }
    this.brandForm = this.formBuilder.group({
      description: ['', Validators.required],
      // product: ['', Validators.required],
      // category: ['', Validators.required],
      status: [''],
      penalty: [''],
    });
  }

  resetFormValidator(): void {
    this.brand = new Brand();
    this.isShowApproveDisapproveButtonByAdmin = false;
    if (!this.isFromModal) {
      this.brand.status = COMMON_FUNCTION.ACTIVE;
    } else {
      this.showApproveRejectButton = false;
      this.isFromModal = false;
    }
    this.brandForm.markAsUntouched();
    this.brandForm.markAsPristine();
  }

  // compareProduct(value: any, selected: any): boolean {
  //   if (value && selected) {
  //     return value.description === selected.description;
  //   }
  // }

  // compareCategory(value: any, selected: any): boolean {
  //   if (value && selected) {
  //     return value.description === selected.description;
  //   }
  // }

  approve() {
    this.brand.status = 'APPROVED';
    this.save(ENDPOINTS.approveBrand);
  }

  reject() {
    this.brand.status = 'DISAPPROVED';
    this.update('');
  }

  closeModal() {
    this.dialogRef.close(this.dataStorage.getS1NewBrand());
  }

  setBrand(brandParam): boolean {
    return brandParam === 'Yes' ? true : false;
  }

  compareBrand(value: any, selected: any): boolean {
    if (value) {
      return value === (selected ? 'Yes' : 'No');
    }
  }

  delete() {
    Swal.fire({
      text: 'Are you sure you want to delete this brand?',
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Confirm'
    }).then((result) => {
      if (result.value) {
        this.apiService.deleteById(ENDPOINTS.brand, this.brand.id).subscribe(
          () => {
            this.sweetAlertService.customSuccessMessage('Brand successfully deleted.');
            this.resetFormValidator();
            this.getList();
          }
        );
      }
    });
  }

}
